.gallery-slider {
  display: grid;
  grid-template-columns: 20% 80%;
  grid-template-rows: 60px;
  padding-left: 5vw;

  @media (max-width: 768px) {
    grid-template-columns: 100%;
    padding-left: 0;
  }

  overflow: hidden;

  &__title {
    grid-area: 1 / 1 / 2 / 2;

    @media (max-width: 768px) {
      grid-area: 1 / 1 / 2 / 2;
      padding-left: 5vw;
    }
  }

  &__controllers {
    grid-area: 2 / 1 / 3 / 2;

    display: flex;
    flex-direction: column;

    @media (max-width: 768px) {
      grid-area: 3 / 1 / 4 / 2;

      flex-direction: row;
      justify-content: space-between;

      padding: 20px 5vw 0 5vw;
    }

    @media (max-width: 425px) {
      padding: 20px;
    }
  }

  &__counter {
    font-size: 36px;
    line-height: 1.1;
    margin-top: auto;

    @media (max-width: 1024px) {
      font-size: 30px;
    }

    @media (max-width: 768px) {
      @include mText;
    }
  }

  &__wrapper {
    grid-area: 1 / 2 / 3 / 3;
    overflow: hidden;

    @media (max-width: 768px) {
      grid-area: 2 / 1 / 3 / 2;
    }
  }

  &__arrow {
    width: 40px;

    &:last-child {
      @include adapt(margin-left, vw(50px), 30px, 50px);
    }

    @media (max-width: 768px) {
      width: 35px;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    object-fit: cover;
    width: 100%;

    &_wrapper {
      padding-top: 60%;
      position: relative;
      margin: 0 10px;
      overflow: hidden;

      @media (max-width: 768px) {
        margin: 0;
      }
    }
  }

  .slick {
    &-list {
      overflow: visible;
    }

    &-slide {
      width: 60vw !important;
      font-size: 0;

      @media (max-width: 768px) {
        width: 100vw !important;
      }
    }
  }
}
